/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* Prevents iPhone from resizing in landscape mode  */
html {
  -webkit-text-size-adjust: none; }

/*  apply a natural box layout model to all elements  */
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

/* BODY  */
body {
  font-size: 16px;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  color: #666;
  background-color: #ececec; 

}

div#wrapper {
  height: 100vh;
  position: relative;
  background: -webkit-gradient(linear, left top, left bottom, from(#f3f6f9), to(#d1ddeb));
  background: -webkit-linear-gradient(#f3f6f9 0%, #d1ddeb 100%);
  background: -o-linear-gradient(#f3f6f9 0%, #d1ddeb 100%);
  background: linear-gradient(#f3f6f9 0%, #d1ddeb 100%); }

/* HEADER  */
header {
  background-color: #54cc6e;
  padding: .5rem 0 .5rem 2%; }

header h1, header h2 {
  color: white; }

header h1 {
  font-size: 1.5rem;
  font-weight: 700; }

header h2 {
  font-size: .75rem;
  font-weight: 400;
  padding-top: .2rem; }

header figure {
  float: right;
  width: 80px;
  padding-right: 2%; }

header figure img {
  width: 100%; }

nav {
  background-color: rgba(155, 201, 219, 0.867);
  padding: 10px 2%; }

  nav .radioLocation{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: end;
        justify-content: flex-end;
  }

 .FLL{
   float: right;
 }

/*end nav*/
.Unit_FlexLayout{
  
  display: -ms-flexbox;
  
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin-left: 25%;
}
.Btn_FlexLayout{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.unit_btn {
  border: none;
  font-weight: bold;
  font-size: 250%;
  text-align: center;
  padding: 0;
  line-height: 50px;
  height: 50px;
  width: 50px;
  background-color: transparent;
}
.button_up{
  color: #c1cbd6;
}
.button_down{
  color: #1f69be;
}
.unit_btn:focus {
  outline: none;
}
/* MAIN  */
main {
  min-height: 5em; }
  main #location {
    margin-top: 3rem;
    text-align: center;
    font-size: 1.2rem;
    font-weight: 700;
    color: #1f69be; }
  main #weather {
    margin-top: .5rem;
    text-align: center;
    font-size: .8rem;
    font-weight: 400; }
  main #temperature {
    margin-top: .75rem;
    text-align: center;
    font-size: 5rem;
    font-weight: 100;
    color: #1f69be; }
  main #desc {
    margin-top: .75rem;
    text-align: center;
    font-size: .9rem;
    font-weight: 400;
    padding: 4%; }
  main #right {
    margin-top: 2rem;
    padding: 0 1rem; }
  main #right img {
    width: 25px; }
  main #right div {
    border-bottom: 1px solid rgba(31, 105, 190, 0.2);
    padding-top: .6rem;
    display: flex;
    display: -ms-flexbox;
    flex-direction: row;
    -ms-flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-align: center;
        align-items: center; }
  main #right div span:nth-child(1) {
    -ms-flex-preferred-size: 40%;
        flex-basis: 40%;
    -ms-flex: 1 1 40%; }
  main #right div span:nth-child(2) {
    -ms-flex-positive: 1;
        flex-grow: 1; }
  main #right div span:nth-child(3) {
    -ms-flex-positive: 1;
        flex-grow: 1; }
  main #right div span:nth-child(4) {
    -ms-flex-positive: 1;
        flex-grow: 1; }

/*  FOOTER  */
footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: left;
  padding: 1em;
  background-color: #54cc6e;
  color: #fff;
  font-size: .9rem; }

.CityRoll_Location{
  padding: 10px;
  font-size: 120%;
  text-align: end;
}
/* OTHER */
div.keepOpen {
  clear: both; }

@media only screen and (min-width: 480px) {
  #left, #right {
    float: left;
    width: 50%; } }
@media only screen and (min-width: 800px) {
  div#wrapper {
    max-width: 1024px;
    margin: 3rem auto 0 auto;
    border: 1px solid #666;
    height: auto;
    -webkit-box-shadow: 10px 10px 10px #999;
            box-shadow: 10px 10px 10px #999; }

  footer {
    clear: both;
    position: relative; } }




